import { Component } from '@angular/core';
import { MeetingScheduleFacade } from './meeting-schedule.facade';
import { APP_ROUTE } from '@core/constants';

@Component({
    selector: 'findqo-meeting-schedule',
    templateUrl: './meeting-schedule.component.html',
    styleUrl: './meeting-schedule.component.scss',
    standalone: false
})
export class MeetingScheduleComponent {
	constructor(private readonly meetingFacade: MeetingScheduleFacade) {}

	meetingList$ = this.meetingFacade.meetingList$;
	readonly appRoute = APP_ROUTE;

	handleFilter($event): void {
		this.meetingFacade.loadMeetingList($event.value).subscribe();
	}
}
