import { Clipboard } from '@angular/cdk/clipboard';
import { Component, Input } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { MeetingResponse } from '@core/models/meeting-schedule.model';
import { MeetingScheduleFacade } from '@feature/meeting-schedule/meeting-schedule.facade';
import { DestroyService } from '@shared/services/destroy.service';
import { takeUntil, tap, timer } from 'rxjs';

@Component({
    selector: 'findqo-meeting-card',
    templateUrl: './meeting-card.component.html',
    styleUrl: './meeting-card.component.scss',
    providers: [DestroyService],
    standalone: false
})
export class MeetingCardComponent {
	constructor(
		private readonly clipboard: Clipboard,
		private readonly destroy: DestroyService,
		private readonly meetingFacade: MeetingScheduleFacade,
		private readonly authService: AuthService
	) {}

	@Input({
		alias: 'meeting',
	})
	set _meeting(data: MeetingResponse) {
		this.meeting = data;
	}

	meeting: MeetingResponse;
	isCopied = false;
	readonly user = this.authService.user();
	readonly appRoute = APP_ROUTE;

	openUrl(url: string): void {
		window?.open(url, '_blank');
	}

	copyUrl(): void {
		this.clipboard.copy(this.meeting.link);
		this.isCopied = true;

		timer(10000)
			.pipe(
				takeUntil(this.destroy),
				tap(() => (this.isCopied = false))
			)
			.subscribe();
	}

	handleStatus(meeting: MeetingResponse): void {
		this.meetingFacade.updateMeetingStatus(meeting.id, +meeting.active ? '0' : '1').subscribe();
	}

	handleDelete(meeting: MeetingResponse): void {
		this.meeting.active = '0';
		this.meetingFacade.deleteMeeting(meeting.id).subscribe();
	}
}
