import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AuthGuard } from '@core/guards/auth.guard';
import { MeetingScheduleComponent } from './meeting-schedule.component';
import { MeetingListGuard } from '@core/guards/meeting-list.guard';

const routes: Routes = [
	{
		path: '',
		component: MeetingScheduleComponent,
		canActivate: [AuthGuard, MeetingListGuard],
	},

	{
		path: APP_ROUTE.meetingSchedule.add,
		loadChildren: () => import('./pages/schedule-meeting/schedule-meeting.module').then((m) => m.ScheduleMeetingModule),
		canActivate: [AuthGuard],
	},

	{
		path: `${APP_ROUTE.meetingSchedule.edit}/:meetingId`,
		loadChildren: () => import('./pages/schedule-meeting/schedule-meeting.module').then((m) => m.ScheduleMeetingModule),
		canActivate: [AuthGuard],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class MeetingScheduleRoutingModule {}
